import { NavLink } from 'react-router-dom';
import styles from './StatisticsAndSpreadsheets.module.css';
import spreadsheets from './data/spreadsheets.json';

export default function StatisticsAndSpreadsheets() {
  return (
    <div>
      <h3 className='heading text-left'> Spreadsheets</h3>
      <div className='container'>
        {spreadsheets.map(link => (
          <NavLink key={link.label} to={link.to} className={styles.link}>
            {link.label}
          </NavLink>
        ))}
      </div>
    </div>
  );
}
